import React from 'react'
import LinkedAnimate from './LinkAnimte'
import './index.css'
import QueueAnim from 'rc-queue-anim';
import Home from './monition/index'
class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {visable:false}
        
    }

    componentDidMount(){
        setTimeout(()=>{
            this.setState({
                visable:true
            })
            console.log("OK")
        },2000)
    }
    render() { 
        return (
            <>
            <Home/> 
                 {/* <QueueAnim className="demo-content">
                {
                    !this.state.visable?null:<LinkedAnimate/>
                }
                </QueueAnim>  */}
            </>
          );
    }
}
 
export default About;