import React from 'react'
import { Form, Input, Button, Checkbox,Radio,notification } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import {register, TipsOk} from '../../api/index'
import './index.css'
class Register extends React.Component {
    constructor(props) {
        super(props);
        this.state = {  value: 1 }
    }
    openNotificationWithIcon = (type,title,msg) => {
        notification[type]({
          message: title,
          description:
             msg,
            duration:5  
        });
      };

    onChange = e => {
        console.log('radio checked', e.target.value);
        this.setState({
          value: e.target.value,
        });
      };

      onFinish = values => {
        register(values).then(res=>{
            if(res.data.code===500){
                this.openNotificationWithIcon('error','失败',res.data.msg)
            }else{
                this.openNotificationWithIcon('success','成功',"注册成功!2秒后返回登录页")
                setTimeout(()=>{
                    this.props.showRegister()
                },2000)
            }  
        })
      };
    render() { 
        return ( 
            
    <Form
    name="normal_login"
    className="login-form"
    initialValues={{
      remember: true,
    }}
    onFinish={this.onFinish}
   
  >
    <p style={{ fontSize: "34px", fontWeight: 900 }}>注册<br/>
    </p>

    <Form.Item
      name="mUsername"
      rules={[
        {
          required: true,
          message: '请输入用户名并且长度在6-18!',
          max:18,
          min:6
        },
      ]}
    >
      <Input type="string" prefix={<UserOutlined className="site-form-item-icon" />} placeholder="登录用户名" />
    </Form.Item>

    <Form.Item
      name="mName"
      rules={[
        {
          required: true,
          message: '请输入昵称并且长度在1-5!',
          max:5,
          min:1
        },
      ]}
    >
      <Input type="string"  prefix={<UserOutlined className="site-form-item-icon" />} placeholder="家庭昵称" />
    </Form.Item> 

    <Form.Item
      name="mPassword"
      rules={[
        {
          required: true,
          message: '请输入密码并且长度在6-18!',
          max:18,
          min:6
        },
      ]}
    >
      <Input
        prefix={<LockOutlined className="site-form-item-icon" />}
        type="password"
        placeholder="密码"
      />
    </Form.Item>


    
    <Form.Item
      name="mPassword2"
      rules={[
        {
          required: true,
          message: '请重复输入密码!',
          max:18,
          min:6
        },
      ]}
    >
      <Input
        prefix={<LockOutlined className="site-form-item-icon" />}
        type="password"
        placeholder="重复密码"
      />
    </Form.Item>
    <Form.Item
      name="mType"
     
      rules={[
        {
      
          required: true,
          message: '请选择身份!',
        },
      ]}
    >
     <Radio.Group onChange={this.onChange} value={this.state.value}>
        <Radio value={"家长"}>家长</Radio>
        <Radio value={"子女"}>子女</Radio>
      </Radio.Group>
    </Form.Item>


    <Form.Item>
      <Button type="primary" htmlType="submit" className="login-form-button">
        立即注册
        </Button>
         &nbsp; &nbsp;  &nbsp; &nbsp; <a onClick={this.props.showRegister}>登录</a>
    </Form.Item>
  </Form>
         );
    }
}
 
export default Register;