import React from 'react'
import { Form, Input, Button, Checkbox,notification } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import './index.css'
import ReactCanvasNest from 'react-canvas-nest';
import { LoginApi } from '../../api/index'
import { cookieLogin } from '../../api/index'
import { HashRouter, Route, Switch,withRouter } from 'react-router-dom'
import  Register from './Register'
import QueueAnim from 'rc-queue-anim';

import {
  inject,
  observer
} from 'mobx-react'

@inject('store') @observer


class Login extends React.Component {


  constructor(props){
  
      super(props);
      this.state = {
        login:true
      }
  
  }

  openNotificationWithIcon = (type,title,msg) => {
    notification[type]({
      message: title,
      description:
         msg,
        duration:5  
    });
  };
  componentWillMount() {
    const token=window.localStorage.token
    if(token!=null){
    cookieLogin(token).then(res => {
      this.props.store.cookieLogin()
      this.props.history.push('/maintable')

    }).catch(e => {
      this.props.history.push('/login')
    })
  }
  }

  showRegister=()=>{
    this.setState({
      login:false
    })
  }

  notShowRegister=()=>{
    this.setState({
      login:true
    })
  }

  // async  _Cookielogin(token){
  //   try {
  //     const res=await cookieLogin(token)
  //     this.props.store.cookieLogin()
  //     this.props.history.push('/maintable')
  //   } catch (e) {

  //   }
  // }


  render() {
    let storage = window.localStorage;
    const token = storage.token

   

    return (
      <div >

        <div style={{ height: "100%", width: "70%", position: "fixed" }}>
          <ReactCanvasNest className='canvasNest' config={{ pointR: 2, pointColor: ' 57,73,171 ', lineColor: ' 57,73,171', follow: true, count: 50, lineWidth: 1, dist: 35000 }} style={{ zIndex: -99 }} />
        </div>
        <div style={{ height: "100%", width: "70%", position: "fixed", right: 0, top: 0 }}>
          <ReactCanvasNest className='canvasNest' config={{ pointR: 2, pointColor: '197,17,98', lineColor: '197,17,98', follow: true, count: 50, lineWidth: 1, dist: 35000 }} style={{ zIndex: -99 }} />
        </div>
        <QueueAnim className="demo-content">
        <div className="loginMain">
      
          <div className="form-back">
        
            {
              this.state.login?  <NormalLoginForm  openNotificationWithIcon={this.openNotificationWithIcon}  showRegister={this.showRegister}  history={this.props.history} store={this.props.store} className="loginChil" />:<Register  showRegister={this.notShowRegister} />
            }
       
        
          
          </div>
         

        </div>
        </QueueAnim>

      </div>
    )
  }
}

export default Login;

const NormalLoginForm = (props) => {


  const onFinish = values => {
    LoginApi(values.username, values.password).then(res => {
      const result = res.data;
      console.log(result)
      if (result.code === 0) {
        props.store.Login(result.msg, result.data[0])
        props.history.push('/maintable')
      } else {
        props.openNotificationWithIcon('error','失败',res.data.msg)
      }
    })
  };



  return (

    <Form
      name="normal_login"
      className="login-form"
      initialValues={{
        remember: true,
      }}
      onFinish={onFinish}
    >
      <p style={{ fontSize: "34px", fontWeight: 900 }}>钱迹<br />
        <p style={{ fontSize: "10px", fontWeight: 500 }}>Family financial management</p>
      </p>

      <Form.Item
        name="username"
        rules={[
          {
            required: true,
            message: '用户名长度应在6-18',
            min:6,
            max:18
          },
        ]}
      >
        <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Username" />
      </Form.Item>
      <Form.Item
        name="password"
        rules={[
          {
            required: true,
            message: '密码长度应在6-18',
            min:6,
            max:18
          },
        ]}
      >
        <Input
          prefix={<LockOutlined className="site-form-item-icon" />}
          type="password"
          placeholder="Password"
        />
      </Form.Item>
      <Form.Item>
        <Form.Item name="remember" valuePropName="checked" noStyle>
          <Checkbox>Remember me</Checkbox>
        </Form.Item>


      </Form.Item>

      <Form.Item>
        <Button type="primary" htmlType="submit" className="login-form-button">
          登录
          </Button>
           &nbsp; &nbsp;  &nbsp; &nbsp; <a onClick={props.showRegister}>注册</a>
      </Form.Item>
    </Form>
  );
};







