import React, { Component } from 'react';
var echarts =require('echarts')

class ShowPageProc extends Component {
    componentDidMount() {
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById(this.props.pageName));
        // 绘制图表

        let data=new Array();
        let dataName=new Array();
        const outcomesum=this.props.pageData
       
       for(let i in outcomesum ) {
            dataName.push(i)
            data.push({
                value:outcomesum[i],
                name:i
            })
       }

       
        myChart.setOption({
            title: {
                text: this.props.dataName,
                subtext: this.props.time[0]+" -- "+this.props.time[1],
                left: 'center'
            },
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b} : {c} ({d}%)'
            },
            legend: {
                orient: 'vertical',
                left: 'left',
                data:  dataName
            },
            series: [
                {
                    name: '',
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '60%'],
                    data: data,
                    emphasis: {
                        itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        })
    }


    render() {
        return (
            <div id={this.props.pageName} style={{ width: 400, height: 400 }}></div>
        );
    }
}

export default ShowPageProc;
