import { observable, computed,action} from "mobx";
import cookie from 'react-cookies'
import moment from 'moment'
class AppStore {
    @observable time = moment().format('YYYY年MM月DD日');
    @observable now=moment().format('HH:mm:ss')
    @observable query=moment().format('HH')
    @observable user={
        token:'',
        userMsg:{
            mname:"",
            mid:''
        }
    }

    @observable quota={}

    //    @observable user={
    //     token:'',
    //     userMsg:{
    //         mname:"null",
    //         mid:2
    //     }
    // }


    @observable orders=[]
    @observable ordersToail={
        income:0,
        outcome:0
    }



    @observable selectItem=[{
        
    }]


    @action Login(token,data){
        let storage = window.localStorage;
        this.user.token=token;
        this.user.userMsg=data
        storage.token=token;
        storage.userMsg=JSON.stringify(data)
    }

    @action cookieLogin(){
        let storage = window.localStorage;
        this.user.token=storage.token
        this.user.userMsg=JSON.parse(storage.userMsg)
    }


    @action loginOut(){
        this.user.token=''
        this.user.userMsg=''
    }


    @action changeTime(){
        this.time = moment().format('YYYY年MM月DD日')
    }

    @action saveOrder(data){
            this.ordersToail.outcome=0
            this.ordersToail.income=0
            data.map(h=>{
                if(h.itype==='支出'){
                    this.ordersToail.outcome+=h.omoney
                }else{
                    this.ordersToail.income+=h.omoney
                }
                h.odate=moment(h.odate).format('YYYY-MM-DD')
            })
            this.orders=data
    }

    @action saveItem(data){
        this.selectItem=data.data
    }

    // @computed get desc(){
        
    //     return `${this.time} has ${this.todos.length}`
    // }
}

const store=new AppStore()

export default store
 
setInterval(()=>{
    store.now=moment().format('HH:mm:ss')
},1000)