import React from 'react'
import moment from 'moment'
import { DatePicker, Modal, Button, Empty, Divider} from 'antd'
import ShowPageSum from './ShowPageSum'
import {getStatis} from '../../api/index'
import ShowPageProc from './ShowPageProc'
const { RangePicker } = DatePicker


class ShowPage extends React.Component {
    constructor(props) {
        super(props);
        let now = moment().format('YYYY-MM-DD');
        let lastmonth = now.substring(0, 7) + '-01'
        this.state = {
            visible: true,
            confirmLoading: false,
            showtable: false,
            date: [lastmonth, now],
            status:''
        }
    }
    handleSelectTime = (value, date) => {
        this.setState({
            date
        })

    }
    handleOk = () => {
        this.setState({
            confirmLoading: true,
           
        });
        getStatis(this.state.date[0],this.state.date[1]).then(res=>{
            console.log(res.data)
            this.setState({
                visible:false,
                showtable: false,
                status:res.data.data,
                confirmLoading: false,
            })
            this.setState({
                showtable:true
            })
        })

    };
    handleCancel=()=>{
        this.setState({
            visible: false,
        })
    }
    showModal=()=>{
        this.setState({
            visible: true,
        })
    }

    render() {
        const dateFormat = 'YYYY-MM-DD'

        const { visible, confirmLoading } = this.state;
        return (
            <>
                <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                    <Button type="primary" onClick={this.showModal}>
                        选择期间
                </Button>
                </div>
                <br/>
                <br/>
                <br/>
                <Modal
                    title="选择期间"
                    visible={visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    confirmLoading={confirmLoading}
                    onCancel={this.handleCancel}
                >

                    <div className="orderMain">
                        <RangePicker
                            onChange={this.handleSelectTime}
                            defaultValue={[moment(this.state.date[0], dateFormat), moment(this.state.date[1], dateFormat)]}
                            format={dateFormat}
                        />
                    </div>
                </Modal>

                {
                    (!this.state.showtable) ? <Empty /> : <div>
                    <Divider orientation="left">总收支统计</Divider>
                        <div style={{ display: 'flex', justifyContent: 'space-around' }}>
                            <ShowPageSum pageName="familySum" dataName="家庭收支情况"  InCome={this.state.status.familyInCome}  OutCome={this.state.status.familyOutCome}  time={this.state.date}/>

                            <ShowPageSum pageName="PersonSum" dataName="个人收支情况"  InCome={this.state.status.personInCome}  OutCome={this.state.status.personOutCome}  time={this.state.date}/>
                        </div>
                        <Divider orientation="left">家庭收支项目占比</Divider>
                        <br/>
                        <div style={{ display: 'flex', justifyContent: 'space-around' }}>
                            <ShowPageProc  pageData={this.state.status.familyOutComeSum} time={this.state.date} pageName="FamilyOutCome" dataName="家庭支出项目占比"/>
                            <ShowPageProc  pageData={this.state.status.familyInComeSum} time={this.state.date} pageName="FamilyInCome" dataName="家庭收入项目占比"/>
                        </div>
                        <Divider orientation="left">个人收支项目占比</Divider>
                        <br/>
                        <div style={{ display: 'flex', justifyContent: 'space-around' }}>
                            <ShowPageProc  pageData={this.state.status.personOutComeSum} time={this.state.date} pageName="personOutCome" dataName="个人支出项目占比"/>
                            <ShowPageProc  pageData={this.state.status.personInComeSum} time={this.state.date} pageName="personInCome" dataName="个人收入项目占比"/>
                        </div>
                    </div>
                }


            </>
        );
    }
}

export default ShowPage;