import React from 'react'
import {Button,Form,Radio, Input,notification} from 'antd'
import {addItem} from '../../api/index'
class AddItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = { type:1,name:'' }
    }
    onChange=e=>{
        const type=e.target.value
        this.setState({type})
    }

    _addItem=e=>{
        let data={
            itype:this.state.type,
            iname:this.state.name,
            status:1
        }   
        addItem(data).then(res=>{
            if(res.data.code===0){
                this.openNotificationWithIcon('success','成功','添加成功了兄弟')
            }else if(res.data.code===302) {
                this.openNotificationWithIcon('error', '失败', '你没有添加的权限')
            }else if(res.data.code===505){
                this.openNotificationWithIcon('error','失败',res.data.msg)
            }else{
                this.openNotificationWithIcon('error','失败','可能参数有误?')
            }
            this.props.handleCancel2()
            this.props._getAllitem()
        })
   
    }

    openNotificationWithIcon = (type,title,msg) => {
        notification[type]({
          message: title,
          description:
             msg,
            duration:5  
        });
      };

    render() { 
        const {type}=this.state
        return (
            <>
        
            <div style={{width:300,height:200}}>
            <Form
                    labelCol={{ span: 5 }}
                    wrapperCol={{ span: 14 }}
                    layout="horizontal"
                   
                >
                     <Form.Item hasFeedback label="类型" rules={[{ required: true }]}>
                        <Radio.Group onChange={this.onChange} value={type} defaultChecked={false}>
                            <Radio value={2}>收入</Radio>
                            <Radio value={1}>支出</Radio>
                        </Radio.Group>
                    </Form.Item>
                    <Form.Item hasFeedback label="名称" rules={[{ required: true }]}>
                        <Input value={this.state.name} onChange={e=>this.setState({name:e.target.value})}/>
                    </Form.Item>
                </Form>
            
                    <Button type="primary"  onClick={this.props.handleCancel2} style={{ position: 'absolute', right: '100px', bottom: '20px' }}>取消</Button>
                    <Button type="primary" onClick={this._addItem} style={{ position: 'absolute', right: '10px', bottom: '20px' }}>确定</Button>
            </div>
            </>
        );
    }
}
 
export default AddItem;