import React from 'react'
import { Table } from 'antd';
import moment from 'moment'
import {
  inject,
  observer
} from 'mobx-react'



const columns = [
  {
    title: '账单名称',
    dataIndex: 'iname',
    // sorter: (a, b) =>  a.iname - b.iname,
    width: '15%',
  },
  {
    title: '类型',
    dataIndex: 'itype',
    filters: [
      { text: '支出', value: '支出' },
      { text: '收入', value: '收入' },
    ],
    width: '10%',
    onFilter: (value, record) => record.itype.indexOf(value) === 0,
    sortDirections: ['descend']
  },
  {
    title: '金额',
    dataIndex: 'omoney',
    width: '10%',
    sorter: (a, b) =>  a.omoney - b.omoney,
    render: (text,record) => <font style={{textAlign:'center'}}>{(record.itype)==='支出'?<font style={{color:'red',fontWeight:500}}>-{text}</font>:<font style={{color:'green',fontWeight:500}}>+{text}</font>}</font>   ,
  },
  {
    title: '创建者',
    
    dataIndex: 'mname',
    width: '10%',
  },
  {
    title: '备注',
    dataIndex: 'otips',
    width: '30%',
  },
  {
    title: '日期',
    sorter: (a, b) =>  moment(a.odate) - moment(b.odate),
    dataIndex: 'odate',
  },
];

const getRandomuserParams = params => {
  return {
    // results: params.pagination.pageSize,
    // page: params.pagination.current,
    ...params,
  };
};

@inject('store') @observer
export default class OrderTable extends React.Component {
  state = {
    data: [],
    pagination: {
      current: 1,
      pageSize: 10,
    },
    loading: false,
  };

  componentDidMount() {
    const { pagination } = this.state;
    // console.log(this.props.store.orders)
    this.setState({
      loading: false,
      data:this.props.store.orders
    })


  }

 

  handleTableChange = (pagination, filters, sorter) => {
    // sortField: sorter.field,
    // sortOrder: sorter.order,
    pagination+=1
    this.setState({
      pagination
    })
    getRandomuserParams(pagination)
  };

  

  render() {
    const { data, pagination, loading } = this.state;
    return (
      <Table
        columns={columns}
        rowKey={record => record.oid}
        dataSource={data}
        pagination={pagination}
        loading={loading}
        onChange={this.handleTableChange}
      />
    );
  }
}
