import React, { useState } from 'react'
import { getItem } from '../../api/index'
import {
  Form,
  Input,
  Button,
  Radio,
  Select,
  DatePicker,
  InputNumber,

} from 'antd';
import moment from 'moment'

import {
  inject,
  observer
} from 'mobx-react'
import Order from '../Order/Order';
@inject('store') @observer
class AddOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [{}]
    }
  }
  componentWillMount() {
    this._getItem(2)

  }

  _getItem = (type) => {
    getItem(type).then(res => {
      this.setState({
        items: res.data.data
      })
    })

  }

  render() {
    // this.props.store.AddOrder()

    return (
      <div>
        <AddForm handleCancel={this.props.handleCancel} handleOk={this.props.handleOk} items={this.state.items} _getItem={this._getItem} />
      </div>
    );
  }
}

export default AddOrder;
const { TextArea } = Input;
const AddForm = (props) => {
  const [componentSize, setComponentSize] = useState('middle');
  const [type, settype] = useState(2);
  const [item, setitem] = useState('')
  const [money, setmoney] = useState('')
  const [tips, settips] = useState('')
  const [time, settime] = useState(moment().format('YYYY-MM-DD'))

  console.log(time)
  const onChange = e => {
    settype(e.target.value)
    setitem('')
    props._getItem(e.target.value)

  };
  function handleSelect(value) {
    console.log(`selected ${value}`);
  }


  const AddOrder=()=>{


  
    const data={
      type,item,money,tips,time
    }
    console.log(data)
    props.handleOk(data)
  }

  const onChangeItem = value => {
    setitem(value)
  }

  const handleSelectTime=(value, date)=>{
      settime(date)
  }

  return (
    <div>
      <Form
        labelCol={{ span: 5 }}
        wrapperCol={{ span: 14 }}
        layout="horizontal"
        initialValues={{ size: componentSize }}
        size={componentSize}
       
      >

        {/* <Form.Item label="Input">
          <Input />
        </Form.Item> */}

        <Form.Item hasFeedback label="类型" rules={[{ required: true }]}>
          <Radio.Group onChange={onChange} value={type} defaultChecked={false}>
            <Radio value={2}>收入</Radio>
            <Radio value={1}>支出</Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item hasFeedback label="项目" rules={[{ required: true }]} >
          <Select onChange={handleSelect} allowClear={true} value={item} onChange={onChangeItem} >
            {
              props.items.map((h, index) =>
                <Select.Option value={h.iid} key={index} >{h.iname}</Select.Option>
              )
            }
          </Select>
        </Form.Item>
        <Form.Item hasFeedback label="金额"  
        
        rules={[
          {
            type: 'number',
            min: 0,
            max: 99,
          },
        ]}
         >
          <InputNumber precision={2} min={0.01} onChange={e=>setmoney(e)} />
        </Form.Item>

        <Form.Item label="备注">
          <TextArea
            value={tips}
            onChange={e=>settips(e.target.value)}
            autoSize={{ minRows: 2, maxRows: 6 }}
            maxLength={20}
          />
        </Form.Item>
        <Form.Item hasFeedback label="日期" rules={[{ required: true }]}>
          <DatePicker
            placeholder={time}
             onChange={handleSelectTime}
          />
        </Form.Item>

      </Form>

      <br />
      <br />

      <div >
        <Button type="primary" onClick={props.handleCancel} style={{ position: 'absolute', right: '100px', bottom: '20px' }}>取消</Button>
        <Button type="primary" onClick={AddOrder} style={{ position: 'absolute', right: '10px', bottom: '20px' }}>确定</Button>
      </div>

    </div>

  );
};