import React from 'react'
import { Layout, Menu, Breadcrumb, Button,notification, Modal,message, Input,InputNumber } from 'antd';
import MSwitch from 'antd/lib/switch'
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom'
import Item from '../Item/Item'
import cookie from 'react-cookies'


import {
  DollarCircleOutlined,
  CalendarOutlined,
  AppstoreOutlined,
  SettingOutlined,
  HomeOutlined,
  LinkOutlined,
  AccountBookOutlined,
  PieChartOutlined
} from '@ant-design/icons';
import Welcome from '../welcome/welcome'
import Order from '../Order/Order'
import Person from '../Person/Person'
import AddOrder from '../Add/AddOrder'
import ShowPage from '../ShowPage/ShowPage'
import About from '../About/About'

import { updatePassword,getItem ,addOrder,cookieLogin,updateQuota,updateName} from '../../api/index'

import './index.css'

import {
  inject,
  observer
} from 'mobx-react'


const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;
@inject('store') @observer


export default class MainTable extends React.Component {
  state = {
    mode: 'inline',
    theme: 'light',
    nowWidth: 256,
    ModalText: 'Content of the modal',
    visible: false,
    confirmLoading: false,
    newOrder: {},
    type: 'left',
    name:'',
    visible2:false,
    visible3:false,
    visible4:false,
    visible5:false,
    password:'',
    password2:''
  };

  openNotificationWithIcon = (type,title,msg) => {
    notification[type]({
      message: title,
      description:
         msg,
        duration:5  
    });
  };
  componentWillMount(){
    let storage = window.localStorage;
     const token= storage.token
   
     this._Cookielogin(token)
      // cookieLogin(token).then(res=>{
      //     if(res.data.status===403){
      //       this.props.history.push('/login')
      //     }else{
      //       this.props.store.cookieLogin()
      //     }
      // }).catch(e=>{
      //   this.props.history.push('/login')
      // })
 
    
  
}

async  _Cookielogin(token){
  try {
    const res=await cookieLogin(token)
    this.props.store.cookieLogin()
    this.props.history.push('/maintable')
    this.setState({
      name:this.props.store.user.userMsg.mname
    })
  } catch (e) {
    this.props.history.push('/login')
}
}


  componentDidUpdate() {

  }

  showModal = () => {
    this.setState({
      visible: true,
      
    });
  };



  _updateName=()=>{
    updateName(this.state.name).then(res=>{
        if(res.data.code===0){
          message.success('修改成功,请重新登陆');
          this.setState({
            visible4:false
          })
          setTimeout(()=>{
            this.loginOut();
          },2000)
        
        }else{
          message.error('修改失败');
        }
    })
  }

  _updatePassword=()=>{
    if(this.state.password!==this.state.password2){
      message.error('两次输入不一致');
    }else{
      updatePassword(this.state.password).then(res=>{
        if(res.data.code===0){
          message.success('修改成功,请重新登陆');
          this.setState({
            visible5:false
          })
          setTimeout(()=>{
            this.loginOut();
          },2000)
        }else{
          message.error('修改失败');
        }
    })
    }
    
  }




  setNewOrder = (newOrder) => {
    this.setState({
      newOrder
    })
  }

  handleOk = (data) => {
    this.setState({
      confirmLoading: true,
    });

    addOrder(data).then(res=>{
      if(res.data.code===0){
        this.setState({
          visible: false,
          confirmLoading: false,
        });
        message.success('记上一笔~');
      }else{
        message.error('添加失败');
      }
    })
    
  };

  handleCancel = () => {
  
    this.setState({
      visible: false,
      visible2:false,
      visible3:false,
      visible4:false,
      visible5:false
    });
  };
  handleUpteQuota=()=>{

    this.setState({
      confirmLoading: true,
    });
    updateQuota(this.props.store.quota).then(res=>{
      if(res.data.code===0){
        this.setState({
          visible3:false,
          confirmLoading: false,
        });
        this.openNotificationWithIcon("success","成功","额度修改成功")
      }else{

        this.setState({
          visible3:false,
          confirmLoading: false,
        });
        this.openNotificationWithIcon("error","失败","额度修改失败")
      }
    })
  }



  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };


  changeMode = value => {
    this.setState({
      mode: value ? 'vertical' : 'inline',
    });
  };

  changeTheme = value => {
    this.setState({
      theme: value ? 'dark' : 'light',
    });
  };

  loginOut = () =>{
      let strong = window.localStorage
      strong.clear();
      this.props.store.loginOut()
      this.props.history.push('/login')

  }
 

  render() {
    const { visible, confirmLoading,visible3,visible4,visible5 } = this.state;
    console.log()
    // const routes = [
    //   { path: '/maintable/welcome', name: 'Welcome', Component: Welcome },
    //   { path: '/maintable/order', name: 'Order', Component: Order },
    //   { path: '/maintable/person', name: 'Person', Component: Person },
    // ]
    
    return (
      <Layout style={{ minHeight: '100vh',height:'100%'}}>
        <Modal
          centered="true"
          width="400"
          title="记一笔"
          visible={visible}

          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
          footer={null}
        >
          <AddOrder handleOk={this.handleOk} handleCancel={this.handleCancel} />
        </Modal>









        <Sider  collapsible width={this.state.nowWidth}  theme={this.state.theme}  >
          <div className="logo" />
          <Menu
            width={this.state.nowWidth}
            defaultSelectedKeys={['1']}
            defaultOpenKeys={['sub1']}
            mode={this.state.mode}
            theme={this.state.theme}
          >
            <Menu.Item key="1" icon={<HomeOutlined />} onClick={target => { this.props.history.push('/maintable/welcome') }} >
              Welcome
          </Menu.Item>
            <Menu.Item key="2" icon={<CalendarOutlined />} onClick={target => { this.props.history.push('/maintable/order') }} >
              家庭账单
          </Menu.Item>

         
            
            
            <Menu.Item key="4"  icon={<SettingOutlined />} onClick={target => { this.props.history.push('/maintable/Item') }} >
             收支项目
          </Menu.Item>

            {/* <SubMenu key="sub2" icon={<SettingOutlined />} title="Navigation Three">
              <Menu.Item key="7">Option 7</Menu.Item>
              <Menu.Item key="8">Option 8</Menu.Item>
              <Menu.Item key="9">Option 9</Menu.Item>
              <Menu.Item key="10">Option 10</Menu.Item>
            </SubMenu> */}
                 
            <Menu.Item key="5"  icon={<PieChartOutlined />} onClick={target => { this.props.history.push('/maintable/showpage') }} >
             统计
            </Menu.Item>
              
            <SubMenu key="sub1" icon={<AccountBookOutlined />} title="个人">
            <Menu.Item key="3"  onClick={target => { this.props.history.push('/maintable/person') }}>账单</Menu.Item>
            <Menu.Item key="6" onClick={e=>this.setState({visible3:true})}>额度设置</Menu.Item>
            <Menu.Item key="7" onClick={e=>this.setState({visible4:true})}>昵称设置</Menu.Item>
            <Menu.Item key="10" onClick={e=>this.setState({visible5:true})}>密码设置</Menu.Item>
            <Menu.Item key="8" onClick={this.loginOut}>注销</Menu.Item>
            </SubMenu>
            <Menu.Item key="link" icon={<LinkOutlined />}  onClick={target => { this.props.history.push('/about') }}  >
              {/* <a href="https://lzhengycy.cn" target="_blank" rel="noopener noreferrer"> */}
               关于
            {/* </a> */}
            </Menu.Item>
          </Menu>
        </Sider>




        <Modal
          title="设置额度"
          visible={visible3}
          width={225}
          onOk={this.handleUpteQuota}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
        >
        <div style={{display:'flex',justifyContent:'center'}}>
          <InputNumber  value={this.props.store.quota.qlimit} min={0}  step={0.2} onChange={e=>this.props.store.quota.qlimit=e} />
        </div>
         
        
        </Modal>


        
        <Modal
          title="修改昵称"
          visible={visible4}
          width={225}
          onOk={this._updateName}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
        >
        {/* <div style={{display:'flex',justifyContent:'center'}}>
          <InputNumber  value={this.props.store.quota.qlimit} min={0}  step={0.2} onChange={e=>this.props.store.quota.qlimit=e} />
        </div> */}
        <div style={{display:'flex',justifyContent:'center'}}>

          <Input  value={this.state.name} min={1}  max={5}  onChange={e=>this.setState({name:e.target.value})} />

        </div>
         
        
        </Modal>


        <Modal
          title="修改密码"
          visible={visible5}
          width={325}
          onOk={this._updatePassword}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
        >
     
       
     <div style={{display:'flex',justifyContent:'center'}}>
          <Input  value={this.state.password} min={1} placeholder="输入新密码" max={18}   type="password" onChange={e=>this.setState({password:e.target.value})} />
    </div>
          <br/>
      
          <div style={{display:'flex',justifyContent:'center'}}>
          <Input  value={this.state.password2} min={1} placeholder="重复输入新密码" max={18} type="password"  onChange={e=>this.setState({password2:e.target.value})} />

          </div>
   

        
        </Modal>


{/* 
        <Modal
          title="修改密码"
          visible={visible4}
          width={225}
          onOk={this.handleUpteQuota}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
        >
        <div style={{display:'flex',justifyContent:'center'}}>
          <InputNumber  value={this.props.store.quota.qlimit} min={0}  step={0.2} onChange={e=>this.props.store.quota.qlimit=e} />
        </div>
         
        
        </Modal> */}



        <Layout className="site-layout" style={{minHeight: '100vh',height:'100%'}}>
          <Header className="site-layout-background" style={{ padding: 0, fontFamily: 700, fontSize: "25px" }}>
            <div style={{ paddingLeft: "2%", fontWeight: '600', fontSize: '37px' }}>钱迹</div>
            <Button onClick={this.showModal} type="primary" shape="round" style={{ position: 'absolute', right: '20px', top: '10px' }} size={'large'}>记一笔</Button>
          </Header>

          <Content style={{ margin: '0 16px' }}>
            <Breadcrumb style={{ margin: '16px 0', fontSize: "30x" }}>
              {/* <p style={{ fontSize:"17px",fontWeight:800 }}>您好,{this.props.store.user.userMsg.mname}</p> */}
            </Breadcrumb>
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360, overflowY: "scroll" }}>
              <Router>
                {/* <AnimatedRouter> */}
                <Switch>
              
              <Route path='/maintable' component={Welcome} exact></Route>
              <Route path='/maintable/welcome' component={Welcome} ></Route>
              <Route path='/maintable/order' component={Order} ></Route>
              <Route path='/maintable/person' component={Person} ></Route>
              <Route path='/maintable/Item' component={Item} ></Route>
              <Route path='/maintable/showpage' component={ShowPage} ></Route>
             
                {/* {routes.map(({ path, Component }) => (
                    <Route key={path}  path={Component}>
                    
                    
                    </Route>
                ))} */}
                </Switch>
                {/* </AnimatedRouter> */}
              </Router>
             
            </div>
          </Content>
          {/* <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer> */}
        </Layout>

        <div class="changestyle">
          <MSwitch onChange={this.changeTheme} />
        </div>

      </Layout>


    );
  }
}
