import React from 'react'
import moment from 'moment'
import { DatePicker, Modal, Button, Empty } from 'antd'
import { getOrder, getOrderByTimeAndMid } from '../../api/index'
import '../Order/index.css'
import MsgTable from './Table/table'

import {
    inject,
    observer
  } from 'mobx-react'

const { RangePicker } = DatePicker
@inject('store') @observer


class Person extends React.Component {
    constructor(props) {
        super(props);
        let now=moment().format('YYYY-MM-DD');
        let lastmonth=now.substring(0,7)+'-01'
        this.state = {
            visible: true,
            confirmLoading: false,
            showtable:false,
            date: [lastmonth, now]
        }
    }
    handleSelectTime = (value, date) => {
        this.setState({
            date
        })

    }
    handleOk = () => {
        this.setState({
            confirmLoading: true,
            showtable:false
        });

        getOrderByTimeAndMid(this.state.date[0], this.state.date[1]).then(res => {
     
            this.props.store.saveOrder(res.data.data)
            this.setState({
                visible: false,
                confirmLoading: false,
                showtable:true
            });
        });

    };
    handleCancel = () => {
      
        this.setState({
            visible: false,
        });
    };

    showModal = () => {
        this.setState({
          visible: true,
        });
      };

    render() {
        const dateFormat = 'YYYY-MM-DD'
        
        const { visible, confirmLoading } = this.state;
        const {ordersToail} = this.props.store
        return (
            <div  >
            <div  style={{display:'flex',justifyContent:'space-between'}}>
                <Button type="primary" onClick={this.showModal}>
                    选择账单日期
                </Button>
                {
                    (!this.state.showtable)?null:
                    <p style={{fontSize:'15px',fontWeight:'600'}} >期间共支出 <font style={{color:'red',fontWeight:'700'}} >{ordersToail.outcome.toFixed(2)}</font>元 ,
                     收入 <font style={{color:'green',fontWeight:'700'}} >{ordersToail.income.toFixed(2)}</font>元
                     </p>
                }
            </div>
            <br/> 
                <Modal
                    title="选择账单日期"
                    visible={visible}
                    onOk={this.handleOk}
                    confirmLoading={confirmLoading}
                    onCancel={this.handleCancel}
                >
               
                    <div className="orderMain">
                        <RangePicker
                            onChange={this.handleSelectTime}
                            defaultValue={[moment(this.state.date[0],dateFormat), moment(this.state.date[1],dateFormat)]}
                            format={dateFormat}
                        />
                    </div>
                </Modal>
                {
                    (!this.state.showtable)?<Empty />:<MsgTable resetGet={this.handleOk}/>
                }
                
            </div>
        );
    }
}
 
export default Person;