import React from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import moment from 'moment'
import { Table, Input, Button, message, Space, notification, Radio, Divider, Modal } from 'antd';
import Highlighter from 'react-highlight-words';
import { SearchOutlined } from '@ant-design/icons';
import { getAllItem, updateItem,delteItem} from '../../api/index'
import EditItem from './editItem'
import AddItem from './additem'

const columns = [
  {
    title: '类型',
    dataIndex: 'itype',
    filters: [
      { text: '支出', value: '支出' },
      { text: '收入', value: '收入' },
    ],
    width: '10%',
    onFilter: (value, record) => record.itype.indexOf(value) === 0,
    sortDirections: ['descend']
  },
  {
    title: '账单名称',
    dataIndex: 'iname',
    // sorter: (a, b) =>  a.iname - b.iname,
    width: '75%',
  }
];

class Item extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: '',
      searchText: '',
      searchedColumn: '',
      selectedRowKeys: [], // Check here to configure the default column
      loading: false,
      selectstudent: [],
      students: [],
      newstudents: [],
      mulit: 0,
      visible2: false,
      visible3: false
    }
  }

  openNotificationWithIcon = (type, title, msg) => {
    notification[type]({
      message: title,
      description:
        msg,
      duration: 5
    });
  };

  handleOk = e => {

    updateItem(e).then(res => {
        if(res.data.code===0){
          this.openNotificationWithIcon('success', '成功', '修改成功了兄弟')
          this._getAllitem();
        } else if(res.data.code===302) {
          this.openNotificationWithIcon('error', '失败', '你没有修改的权限')
        }else{
          this.openNotificationWithIcon('error', '失败', '发生未知错误')
        }
    })

    if (this.state.newstudents != '') {
      this.setState({
        visible: false,
        selectstudent: this.state.newstudents
      });
    } else {
      this.setState({
        visible: false,
      });
    }


  };



  handleCancel = e => {

    this.setState({
      visible: false,
      visible2: false,
      visible3: false
    });
  };

  inserthandleCancel = e => {

    this.setState({
      visible2: false,
    });
  };


  _getAllitem = () => {
    getAllItem().then(res => {
      this.setState({
        data: res.data.data
      })
    })
  }

  componentDidMount() {
    this._getAllitem();
  }
  onSelectChange = selectedRowKeys => {
    let selectstudent = new Array()
    this.state.data.forEach((n, index) => {
      if (selectedRowKeys.includes(n.iid)) {
        selectstudent.push(n)
        console.log(n)
      }
    })

    this.setState({ selectedRowKeys, selectstudent });
  };


  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Search
              </Button>
          <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reset
              </Button>
        </Space>
      </div>
    ),
    filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
    onFilter: (value, record) =>
      record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select());
      }
    },
    render: text =>
      this.state.searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text.toString()}
        />
      ) : (
          text
        ),
  });

  add = () => {
    this.setState({
      visible2: true
    })
  }
  edit = () => {
    this.setState({
      visible: true
    })
  }
  delete = () => {
    this.setState({
      visible3: true
    })

  }
  deleteOK=()=>{
    let iids=[];
    this.state.selectstudent.forEach((h,index)=>{
      iids.push(h.iid)
    })
    delteItem(iids).then(res=>{
      if(res.data.code===0){
        this.openNotificationWithIcon('success', '成功', '删除成功了兄弟')
        this._getAllitem();
      } else if(res.data.code===302) {
        this.openNotificationWithIcon('error', '失败', '你没有权限删除')
      }else{
        this.openNotificationWithIcon('error', '失败', '发生未知错误')
      }
      this.handleCancel()
      this._getAllitem()
    })
  }


  render() {
    const { loading, selectedRowKeys, data, pagination } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const hasSelected = selectedRowKeys.length > 0;
    return (
      <div>
        <div style={{ marginBottom: 16 }}>
          <Button type="primary" onClick={this.edit} disabled={!(hasSelected && (selectedRowKeys.length < 2))} >
            修改
                </Button>
                &nbsp;
                &nbsp;
                &nbsp;
                <Button type="primary" danger onClick={this.delete} disabled={!hasSelected} loading={loading}>
            删除
                </Button>
                &nbsp;
                &nbsp;
                &nbsp;
                <Button type="primary" onClick={this.add}  >
            添加
                </Button>
          <span style={{ marginLeft: 8 }}>
            {hasSelected ? `选中 ${selectedRowKeys.length} 项` : ''}
          </span>
        </div>

        <Modal
          title="修改"
          visible={this.state.visible}

          onCancel={this.handleCancel}
          destroyOnClose="true"
          width="400"
          centered="true"
          footer={null}
        >
          <EditItem handleCancel={this.handleCancel} handleOk={this.handleOk} item={this.state.selectstudent[0]} parent={this} />
        </Modal>



        <Modal
          title="添加收支项目"
          visible={this.state.visible2}

          onCancel={this.handleCancel}
          destroyOnClose="true"
          width="400"
          centered="true"
          footer={null}
        >
          <AddItem  _getAllitem={this._getAllitem} handleCancel2={this.handleCancel} handleOk={this.handleAddOk} parent={this} />
        </Modal>



        <Modal
          title="删除项目"
          visible={this.state.visible3}
          onCancel={this.handleCancel}
          destroyOnClose="true"
          width="400"
          centered="true"
          onOk={this.deleteOK}
        >
          <p style={{ textAlign: 'center', color: 'red', fontSize: '20px' }} >警告</p>
          <p style={{ textAlign: 'center',  fontSize: '18px' }} >删除项目后与之关联的账单也会删除,是否确认.</p>
        </Modal>



        <Table
          columns={columns}
          rowKey={record => record.iid}
          dataSource={data}
          pagination={pagination}
          loading={loading}
          rowSelection={rowSelection}

        />
      </div>
    );
  }
}

export default Item;