import React, { Component } from 'react';
var echarts =require('echarts')

class ShowPageSum extends Component {
    componentDidMount() {
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById(this.props.pageName));
        // 绘制图表
        myChart.setOption({
            title: {
                text: this.props.dataName,
                subtext: this.props.time[0]+" -- "+this.props.time[1],
                left: 'center'
            },
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b} : {c} ({d}%)'
            },
            legend: {
                orient: 'vertical',
                left: 'left',
                data: ['支出', '收入']
            },
            series: [
                {
                    name: '',
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '60%'],
                    data: [
                        {value: this.props.OutCome, name: '支出'},
                        {value: this.props.InCome, name: '收入'}, 
                    ],
                    emphasis: {
                        itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        })
    }


    render() {
        return (
            <div id={this.props.pageName} style={{ width: 400, height: 400 }}></div>
        );
    }
}

export default ShowPageSum;
