import React,{Component} from 'react'
import {Switch,Route,Redirect} from 'react-router-dom'
import moment from 'moment'
import { Table, Input, Button, message,Space, Radio, Divider,Modal,notification} from 'antd';
import Highlighter from 'react-highlight-words';
import { SearchOutlined } from '@ant-design/icons';
import AddOrder from './edit'
import {getOrder,deleteOrders,updateOrder} from '../../../api/index'
import QueueAnim from 'rc-queue-anim';
import {
    inject,
    observer
  } from 'mobx-react'

  @inject('store') @observer
class MsgTable extends Component {
    state = {
        searchText: '',
        searchedColumn: '',
        selectedRowKeys: [], // Check here to configure the default column
        loading: false,
        selectstudent:[],
        students:[],
        newstudents:[],
        mulit:0,
      };
      
      showModal = () => {
        this.setState({
          visible: true,
        });
      };


      openNotificationWithIcon = (type,title,msg) => {
        notification[type]({
          message: title,
          description:
             msg,
            duration:5  
        });
      };

      handleOk = e => {
        
        updateOrder(e).then(response=>{
            if(response.data.code===0){
              this.openNotificationWithIcon('success','成功','修改成功了兄弟')
              this.props.resetGet()
            }else{
                this.openNotificationWithIcon('error','失败','参数输入可能有误?')
            }
        })
      
        if(this.state.newstudents!=''){
            this.setState({
                visible: false,
                selectstudent:this.state.newstudents
              });
        }else{
            this.setState({
                visible: false,
              });
        }
       
       
      };
    
      handleCancel = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
      };
      inserthandleCancel = e => {
        
        this.setState({
          visible2: false,
        });
      };
    

      

      componentWillMount(){
        let result=this.props.store.orders
        result.map((h,index)=>{
            h.key=index
           
        })
        this.setState({
            loading: false,
            students:result
          })
      };


      getNewStudent=(_this,newstudents)=>{
              this.setState({
                newstudents
              })
      }

      edit=()=>{
        this.showModal()
      }

      handleDelete = (keys) => {
        const dataSource = [...this.state.students];
        this.setState({
            students: dataSource.filter(item =>(keys.indexOf(item.key)==-1) ),
            selectedRowKeys: [],
            loading: false,
            // selectstudent:[],
            mulit:this.state.mulit+1
        });
    }


   start = () => {
        let needDelte=new Array()
        this.setState({ loading: true });
        this.state.selectstudent.forEach(h=>{
            
            needDelte.push(h.oid)
                
        })
        deleteOrders(needDelte).then(res=>{
          console.log(res)
            if(res.data.code!=0){
                message.error('删除失败');
            }else{
                message.success('删除成功');
                this.handleDelete(this.state.selectedRowKeys)
            }
        })
        
      
      };

      onSelectChange = selectedRowKeys => {
        let  selectstudent=new Array()
        this.state.students.forEach((n,index)=>{
            if(selectedRowKeys.includes(n.key)){
                selectstudent.push(n)
            }
        })
        console.log("选中:"+selectedRowKeys)
        this.setState({ selectedRowKeys, selectstudent });
      };


      getColumnSearchProps = dataIndex => ({
        filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
          <div style={{ padding: 8 }}>
            <Input
              ref={node => {
                this.searchInput = node;
              }}
              placeholder={`Search ${dataIndex}`}
              value={selectedKeys[0]}
              onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
              onPressEnter={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
              style={{ width: 188, marginBottom: 8, display: 'block' }}
            />
            <Space>
              <Button
                  type="primary"
                onClick={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
                icon={<SearchOutlined />}
                size="small"
                style={{ width: 90 }}
              >
                Search
              </Button>
              <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
                Reset
              </Button>
            </Space>
          </div>
        ),
        filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
        onFilter: (value, record) =>
          record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: visible => {
          if (visible) {
            setTimeout(() => this.searchInput.select());
          }
        },
        render: text =>
        this.state.searchedColumn === dataIndex ? (
          <Highlighter
            highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
            searchWords={[this.state.searchText]}
            autoEscape
            textToHighlight={text.toString()}
          />
        ) : (
          text
        ),
    });
  
    handleSearch = (selectedKeys, confirm, dataIndex) => {
      confirm();
      this.setState({
        searchText: selectedKeys[0],
        searchedColumn: dataIndex,
      });
    };
  
    handleReset = clearFilters => {
      clearFilters();
      this.setState({ searchText: '' });
    };
  


    render() {
        
        const { loading, selectedRowKeys } = this.state;
        const rowSelection = {
          selectedRowKeys,
          onChange: this.onSelectChange,
        };
        const hasSelected = selectedRowKeys.length > 0;
        
        const columns = [
            {
                title: '账单名称',
                dataIndex: 'iname',
                // sorter: (a, b) =>  a.iname - b.iname,
                width: '15%',
              },
              {
                title: '类型',
                dataIndex: 'itype',
                filters: [
                  { text: '支出', value: '支出' },
                  { text: '收入', value: '收入' },
                ],
                width: '10%',
                onFilter: (value, record) => record.itype.indexOf(value) === 0,
                sortDirections: ['descend']
              },
              {
                title: '金额',
                dataIndex: 'omoney',
                width: '10%',
                sorter: (a, b) =>  a.omoney - b.omoney,
                render: (text,record) => <font style={{textAlign:'center'}}>{(record.itype)==='支出'?<font style={{color:'red',fontWeight:500}}>-{text}</font>:<font style={{color:'green',fontWeight:500}}>+{text}</font>}</font>   ,
              },
              {
                title: '创建者',
                
                dataIndex: 'mname',
                width: '10%',
              },
              {
                title: '备注',
                dataIndex: 'otips',
                width: '30%',
              },
              {
                title: '日期',
                sorter: (a, b) =>  moment(a.odate) - moment(b.odate),
                dataIndex: 'odate',
                render:(text,record)=>moment(text).format('YYYY-MM-DD')
              }
        ];
       
        return (
        
        <div>
            <div style={{ marginBottom: 16 }}>
                 <Button type="primary"  onClick={this.edit} disabled={!(hasSelected&&(selectedRowKeys.length<2))} >
                    修改
                </Button>
                &nbsp;
                &nbsp;
                &nbsp;
                <Button type="primary" danger onClick={this.start} disabled={!hasSelected} loading={loading}>
                    删除
                </Button>
                &nbsp;
                &nbsp;
                &nbsp;
          
                <span style={{ marginLeft: 8 }}>
                    {hasSelected ? `选中 ${selectedRowKeys.length} 项` : ''}
                </span>
            </div>
            <Modal
                title="修改信息"
                visible={this.state.visible}
                
                onCancel={this.handleCancel}
                destroyOnClose="true"              
                width="400"
                centered="true"
                footer={null}
                >
                <AddOrder   handleOk={this.handleOk}  parent={ this }  order={this.state.selectstudent[0]} />
            </Modal>
            <QueueAnim delay={300} className="queue-simple">
            <Table  rowKey={record =>record.key} rowSelection={rowSelection} columns={columns} dataSource={this.state.students}/>
            </QueueAnim>
       </div>
        );
      }
}
 
export default MsgTable;