import axios from 'axios'
import qs from 'qs'
import store from '../store/index'
const BaseUrl = 'http://localhost:8080'




export const LoginApi = (u, p) => {
    return axios.post(BaseUrl + "/login", qs.stringify({
        username: u,
        password: p
    }))
}


export const getOrderByTime = (startDate, endDate) => {
    var data = qs.stringify({
        start: startDate,
        end: endDate
    });
    return axios.post(BaseUrl + "/order/getbytime", data, { headers: { 'authorization': 'bearer;' + store.user.token } })
}

export const getOrderByTimeAndMid = (startDate, endDate) => {
    var data = qs.stringify({
        start: startDate,
        end: endDate,
        mid: store.user.userMsg.mid
    });
    return axios.post(BaseUrl + "/order/getbytimeandmid", data, { headers: { 'authorization': 'bearer;' + store.user.token } })
}


