export const getItem = (type) => {
    var data = qs.stringify({
        type: Number(type)
    });
    return axios.post(BaseUrl + "/item/getitem", data, { headers: { 'authorization': 'bearer;' + store.user.token } })
}

export const getAllItem = () => {
    return axios.post(BaseUrl + "/item/getallitem", null, { headers: { 'authorization': 'bearer;' + store.user.token } })
}

export const addOrder = (order) => {
    var data = qs.stringify({
        item: order.item,
        type: order.type,
        money: order.money,
        time: order.time,
        tips: order.tips,
        mid: store.user.userMsg.mid
    });
    return axios.post(BaseUrl + "/order/addorder", data, { headers: { 'authorization': 'bearer;' + store.user.token } })

}


export function deleteOrders(oids) {

    let data = {
        oids: oids,
        mid: store.user.userMsg.mid
    };
    return axios(
        {
            url: BaseUrl + "/order/delete",
            method: 'DELETE',
            data: data,
            headers: {
                'Content-Type': 'application/json',
                'authorization': 'bearer;' + store.user.token
            }
        }
    )


}


export function register(member) {

    console.log(member)
    return axios(
        {
            url: BaseUrl + "/register",
            method: 'POST',
            data: member
        }
    )


}

export const updateOrder = (order) => {
    order = {
        ...order,
        mid: store.user.userMsg.mid
    }

    return axios(
        {
            url: BaseUrl + "/order/update",
            method: 'PUT',
            data: order,
            headers: {
                'Content-Type': 'application/json',
                'authorization': 'bearer;' + store.user.token
            }
        }
    )
}



