import React, { useState } from 'react'
import moment from 'moment'
import {
  Form,
  Input,
  Button,
  Radio,
  Select,
  DatePicker,
  InputNumber,

} from 'antd';


import {
  inject,
  observer
} from 'mobx-react'

@inject('store') @observer
class EditItem extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [{}]
    }
  }
  componentWillMount() {

    
  }

 

  render() {
    // this.props.store.AddOrder()

    return (
      <div>
        <AddForm handleCancel={this.props.handleCancel} handleOk={this.props.handleOk} item={this.props.item}  />
      </div>
    );
  }
}

export default EditItem;
const { TextArea } = Input;

const AddForm = (props) => {

  const [componentSize, setComponentSize] = useState('middle');
  let selecttype=2
  if(props.item.itype==='支出'){
      selecttype=1
  }
  
  const [type, settype] = useState(selecttype);
  const [iname,setiname]= useState(props.item.iname);


  const onChange = e => {
    settype(e.target.value)
   

  };


  const UpdateOrder=()=>{
    
    const data={
      itype:type,
      iname:iname,
      status:1,
      iid:props.item.iid,
    }
 
    props.handleOk(data)
  }



  return (
    <div>
      <Form
        labelCol={{ span: 5 }}
        wrapperCol={{ span: 14 }}
        layout="horizontal"
        initialValues={{ size: componentSize }}
        size={componentSize}
      >


        <Form.Item hasFeedback label="类型" rules={[{ required: true }]}>
          <Radio.Group onChange={onChange}  disabled={true} value={type} defaultChecked={false}>
            <Radio value={2}>收入</Radio>
            <Radio value={1}>支出</Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item hasFeedback label="项目" rules={[{ required: true }]} >
            <Input value={iname} onChange={target=>setiname(target.target.value)}/>
        </Form.Item>
     </Form>

      <br />
      <br />

      <div>
        <Button type="primary" onClick={props.handleCancel} style={{ position: 'absolute', right: '100px', bottom: '20px' }}>取消</Button>
        <Button type="primary" onClick={UpdateOrder} style={{ position: 'absolute', right: '10px', bottom: '20px' }}>确定</Button>
      </div>

    </div>

  );
};