import React from 'react'
import { Calendar, Card, Button, Modal, Form, Input, Progress, Tooltip } from 'antd';
import {
    FormOutlined
} from '@ant-design/icons'


import { getWeather, getTips, TipsOk, addTip, getQuota } from '../../api/index'
import './index.css'
import {
    inject,
    observer
} from 'mobx-react'
const welcomeButton = {
    position: "absolute",
    bottom: "5px",
    right: "5px"
}
const welcomeCard = {
    flex: 1,
    width: "30.3%",
    minWidth: "30.3%",
    maxWidth: "30.3%",
    height: "210px",
    padding: "10px",
    margin: "10px auto",
}
const { TextArea } = Input;
@inject('store') @observer
class Welcome extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            selectID: ''
            , confirmLoading: false
            , one: '', weather: ''
            , show: false
            , tips: []
            , visible: false
            , visible2: false
            , title: ''
            , body: '',
            quota: ''
        }
    }



    showAddTips = () => {

        this.setState({ visible2: true })
    }

    componentDidMount() {
        const jinrishici = require('jinrishici');
        jinrishici.load(result => {
            this.setState({
                one: result.data.content
            });
        });
        getWeather().then(res => {

            this.setState({
                weather: res.data
            });
        })
        this._getTip();
        console.log("cck:"+this.props.store.user.userMsg.mid)
        this.props.store.cookieLogin()
        getQuota().then(res => {
            
            this.setState({
                quota: res.data.data
            })
            this.props.store.quota=res.data.data
        })
    }

    
    _getTip = () => {
        getTips().then(res => {
            this.setState({
                tips: res.data.data
            })
        })
    }


    handleTipsOk = () => {
        this.setState({
            confirmLoading: true,

        })
        TipsOk(this.state.selectID).then(res => {
            if (res.data.code === 0) {
                let tip = this.state.tips
                tip = tip.filter(h => h.tid !== this.state.selectID)
                this.setState({
                    visible: false,
                    tips: tip,
                    selectID: '',
                    confirmLoading: false,
                })
            }
        })


    }

    handleCancel = () => {
        this.setState({
            visible2: false,
            visible: false
        });
    };

    handlerOk = (id) => {
        this.setState({
            selectID: id,
            visible: true
        })
    }
    handleTipsAdd = () => {
        this.setState({
            confirmLoading: true,

        })
        addTip(this.state.title, this.state.body).then(res => {
            if (res.data.code === 0) {
                this.setState({
                    confirmLoading: false,
                    visible2: false
                })
                this._getTip();
            } else {
                alert("添加失败")
            }
        }).catch(e => {
            alert("添加失败")
        })

    }

    render() {
        const quota = this.state.quota
        const number = ((quota.qexpenses / quota.qlimit) * 100).toFixed(2)
        return (
            <div>

                <Modal
                    title="提醒"
                    visible={this.state.visible}
                    onOk={this.handleTipsOk}
                    confirmLoading={this.state.confirmLoading}
                    onCancel={this.handleCancel}

                >
                    <p style={{ textAlign: 'center', fontSize: '20px' }}>您确定完事了是吗?</p>
                </Modal>

                <Modal
                    title="添加便签"
                    visible={this.state.visible2}
                    confirmLoading={this.state.confirmLoading}
                    onOk={this.handleTipsAdd}
                    onCancel={this.handleCancel}
                >
                    <div style={{ display: 'flex', justifyContent: 'center' }}>
                        <Form>
                            <Form.Item label="标题">
                                <Input maxLength='30'
                                    value={this.state.title}
                                    onChange={e => this.setState({
                                        title: e.target.value
                                    })}
                                />
                            </Form.Item>

                            <Form.Item label="正文">
                                <TextArea
                                    value={this.state.body}
                                    onChange={e => this.setState({
                                        body: e.target.value
                                    })}
                                    autoSize={{ minRows: 2, maxRows: 4 }}
                                    maxLength='100'
                                />
                            </Form.Item>

                        </Form>

                    </div>



                </Modal>





                <div className="welcomeMain">
                    <div>
                        {this.props.store.query > 19 ? <p style={{ fontSize: "40px" }}>晚上好,{this.props.store.user.userMsg.mname}</p> : this.props.store.query > 12 ? <p style={{ fontSize: "40px" }}>中午好,{this.props.store.user.userMsg.mname}</p> : this.props.store.query > 6 ? <p style={{ fontSize: "40px" }}>早上好,{this.props.store.user.userMsg.mname}</p> : <p style={{ fontSize: "40px" }}>晚上好,{this.props.store.user.userMsg.mname}</p>}
                        <p style={{ fontSize: "18px" }}>现在是: {this.props.store.time} &nbsp;&nbsp;{this.props.store.now}</p>
                        <div>
                            <p style={{ fontSize: "18px", fontWeight: 900 }}>
                                实时&nbsp;{this.state.weather.city}&nbsp;天气:
                        </p>
                            <p>{this.state.weather.tem}°&nbsp; &nbsp;  &nbsp; &nbsp;{this.state.weather.wea}&nbsp; &nbsp;&nbsp; &nbsp;{this.state.weather.win}</p>

                        </div>
                        <br />
                        <br />
                        <p>{this.state.one}</p>
                    </div>
                    <div>

                        {
                            number < 100 ?
                                <Tooltip title={"本月额度使用:" + quota.qexpenses + "/" + quota.qlimit}>
                                    <Progress type="circle"
                                        strokeColor={{
                                            '0%': '#87d068',
                                            '100%': '#87d068',
                                        }}
                                        percent={number}
                                        format={percent => `额度使用${percent} % `}
                                        status="active" />
                                </Tooltip> : <Tooltip title={"本月额度使用:" + quota.qexpenses + "/" + quota.qlimit}>
                                    <Progress type="circle"
                                        strokeColor={{
                                            '0%': 'red',
                                            '100%': 'red',
                                        }}
                                        percent={(number-100).toFixed(2)}
                                        format={percent => `超额使用${percent} % `}
                                        status="active" />
                                </Tooltip>

                        }

                    </div>

                    <div className="site-calendar-demo-card">
                        <Calendar fullscreen={false} />
                    </div>

                </div>


                <p style={{ fontSize: "15px", fontWeight: 700 }} >家庭便签  &nbsp;&nbsp;<Button type="primary" onClick={this.showAddTips} shape="round" icon={<FormOutlined />} /></p>

                <div className="site-card-border-less-wrapper">

                    {
                        this.state.tips.map((h) => {
                            return (

                                <Card title={h.ttitle} bordered={false} style={welcomeCard}>
                                    <p>{h.tbody}</p>
                                    <Button type="primary" danger onClick={() => this.handlerOk(h.tid)} style={welcomeButton}>
                                        完事了
                                        </Button>
                                </Card>
                            )
                        })
                    }

                </div>

            </div>
        );
    }
}

export default Welcome;